.widget {
  display: flex;
  justify-content: space-between;
  flex: 1;
  padding: 10px;
  box-shadow: 2px 4px 10px 2px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  height: 100px;

  .left,
  .right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .title {
    font-weight: bold;
    font-size: 14px;
    color: grey;
  }
  .counter {
    font-size: 28px;
    font-weight: 300;
  }

  .link {
    width: max-content;
    font-size: 12px;
    border-bottom: 1px solid grey;
    cursor: pointer;
  }

  .percentage {
    display: flex;
    align-items: center;
    font-size: 14px;

    &.positive{
        color:green;
    } 

    &.negative{
        color:red;
    } 
  }

  .icon {
    font-size: 32px;
    padding: 5px;
    border-radius: 5px;
    align-self: flex-end;
  }
}
